import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { IGTMService } from 'src/app/providers/Api/IGTM.service';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalController, IonContent } from '@ionic/angular';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { Socket } from 'ngx-socket-io';
import { IgtmplantillaPage } from '../igtmplantilla/igtmplantilla.page';
import { ModalMegustasComponent } from 'src/app/Components/modal-megustas/modal-megustas.component';

@Component({
  selector: 'app-igtmpublic',
  templateUrl: './igtmpublic.page.html',
  styleUrls: ['./igtmpublic.page.scss'],
})
export class IgtmpublicPage implements OnInit {

  publicaciones = [];
  cargadentro = false;

  click = false;

  cargaTodo = false;

  tipo = '';

  @ViewChild('contenido', {static: false}) contenido: IonContent;
  
  constructor(
    private IGTMApi: IGTMService,
    private almacenamiento: InicioService,
    private activeRoute: ActivatedRoute,
    private modalCtrl: ModalController,
    private statusbar: StatusBar,
    private toast: ToastServices,
    private socket: Socket,
    private router: Router
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {

    this.tipo = this.activeRoute.snapshot.paramMap.get('tipo');
    this.publicaciones = [];
    if (this.activeRoute.snapshot.paramMap.get('tipo') === 'historia') {
      this.iniciar2();
    } else {
      this.iniciar();
    }

   

  }

  async iniciar() {
    const info = await this.almacenamiento.obtenerToken();
   
    if ( info ) {

      
      this.IGTMApi.leerUna(info.token, {id: this.activeRoute.snapshot.paramMap.get('id')}).subscribe( async (data: any) => {
        // console.log(data)
        if (data.exe) {
          if (data.response) {
            data.response.guardado = false;
          if (data.response.creador.imagen.length > 0) {
              data.response.image = `https://motocaliapp.com/${data.response.creador.imagen[0].path}`;
            } else {
              data.response.image = '/assets/notfound.png';
            }
  
          if (data.response.creadororiginal) {
              data.response.cog = true;
              if (data.response.creadororiginal.imagen.length > 0) {
                data.response.imageori = `https://motocaliapp.com/${data.response.creadororiginal.imagen[0].path}`;
              } else {
                data.response.imageori = '/assets/notfound.png';
              }
            } else {
              data.response.cog = false;
            }
  
           
  
          await new Promise((resolve, reject) => {
              this.IGTMApi.leerFotos(info.token, {id: data.response._id}).subscribe((res: any) => {
           
              if ( res.exe ){
                data.response.archivos = res.response;
                // console.log(res.response.length, 'Bien fotos')
                if (res.response.length > 1) {
                  data.response.pager = true;
                  resolve({exe: true})
                } else {
                  resolve({exe: false})
                  data.response.pager = false;
                }
              } else {
                resolve({exe: true})
                data.response.archivos = []
              }
  
              this.cargadentro = true;
  
            }, (err) => {
              resolve({exe: false})
              data.response.archivos = [];
            })
          })
  
          await new Promise((resolve, reject) => {
            this.IGTMApi.crearGuardado(info.token, {publicacion: data.response._id, tipo: 'leer'}).subscribe((res: any) => {
               if (res.exe) {
                data.response.guardado = true;
               } else {
                data.response.guardado = false;
               }
               resolve(true)
  
            }, (err) => {
              resolve(true)
              data.response.guardado = false;
            })
          });
  
          await new Promise((resolve, reject) => {
            this.IGTMApi.meGusta(info.token, {id: data.response._id, tipo: 'leer'}).subscribe((res: any) => {
           
              if ( res.response.find((itemmg: any) => itemmg.creador._id === info._id)) {
                data.response.megusta = true;
              } else {
                data.response.megusta = false;
              }
              resolve(true)
              data.response.valormg = res.response.length;
              data.response.mglista = res.response;
  
            }, (err) => {
              resolve(true)
              data.response.megusta = false;
              data.response.valormg = '';
            })
          });
  
  
          await new Promise((resolve, reject) => {
            this.IGTMApi.crearComentario(info.token, {_id: data.response._id, tipo: 'leer'}).subscribe((res: any) => {
  
              // console.log(res.response, 'comentarios', data.response._id)
           
              if ( res.exe ){
                data.response.comentarios = res.response;
                if (res.response.length > 0) {
                  data.response.msg = `${data.response.comentarios.length} comentarios...`;
                } else {
                  data.response.msg = 'No hay ningún comentario paraesta publicación';
                }
              } else {
                data.response.comentarios = []
              }
              resolve(true);
              this.cargadentro = true;
  
            }, (err) => {
              resolve(true);
              data.response.comentarios = [];
            })
          });
  
         
            // if(item.comentario !== '') {
            //   let array = item.comentario.split(' ');
            //   // console.log(array, 'array')
            //   array = array.map((arr: string) => {
            //     if (arr.charAt(0) === '@') {
            //       let viaje = arr.substr(1);
            //       arr = `<a class="enlace" href="/central/buscar/perfil-visita/${viaje}">${arr}</a>`;
            //       // console.log(arr);
  
                  
            //     }
  
            //     return arr;
            //   })
  
              
  
            //   array = array.join(' ');
            //   // console.log(array);
            //   item.comentario = array;
            // }
            
          this.publicaciones.push(data.response);
          } else {
            this.publicaciones = [];
          }
        } else {
  
        }
  
        this.cargaTodo = true;
      }, (err) => {
  
      })
    }
  }
  async iniciar2() {
    const info = await this.almacenamiento.obtenerToken();
   
    if ( info ) {

      
      this.IGTMApi.leerUna2(info.token, {id: this.activeRoute.snapshot.paramMap.get('id')}).subscribe( async (data: any) => {
        // console.log(data, 'DATA PRATA')
        if (data.exe) {
         if (data.response) {
          if (data.response.creador.imagen.length > 0) {
            data.response.image = `https://motocaliapp.com/${data.response.creador.imagen[0].path}`;
          } else {
            data.response.image = '/assets/notfound.png';
          }
        await new Promise((resolve, reject) => {
            this.IGTMApi.leerFotos(info.token, {id: data.response._id}).subscribe((res: any) => {
         
            if ( res.exe ){
              data.response.archivos = res.response;
              // console.log(res.response.length, 'Bien fotos')
              if (res.response.length > 1) {
                data.response.pager = true;
                resolve({exe: true})
              } else {
                resolve({exe: false})
                data.response.pager = false;
              }
            } else {
              resolve({exe: true})
              data.response.archivos = []
            }

            this.cargadentro = true;

          }, (err) => {
            resolve({exe: false})
            data.response.archivos = [];
          })
        })

      
       
          
        this.publicaciones.push(data.response);
         } else {
           this.publicaciones = [];
         }
        } else {
  
        }
  
        this.cargaTodo = true;
      }, (err) => {
  
      })
    }
  }
  
  async compartir( publicacion ) {
    const modal = await this.modalCtrl.create({
      component: IgtmplantillaPage,
      componentProps: {
        tipo: 'compartir',
        publicacionc: publicacion,
      }
    })
    await modal.present();
  
    const cerrado = await modal.onWillDismiss();
    if (cerrado) {
        this.statusbar.show();
        this.click = false;
  
        if (cerrado.data) {
          if (cerrado.data.recargo) {
            await this.contenido.scrollToTop();
          }
        }
      }
  }

  async guardado(publicacion: any, tipo, i) {
    if (tipo === 'crear') {
      this.publicaciones[i].guardado = true;
    } else {
      this.publicaciones[i].guardado = false;
    }
    const info = await this.almacenamiento.obtenerToken();
   
    if ( info ) {
      this.IGTMApi.crearGuardado(info.token, {tipo, publicacion: publicacion._id}).subscribe((data: any) => {
        if (data.exe) {
          if ( tipo === 'crear') {
            this.toast.toastNotificacion('Publicación guardada en colecciones');
           } else {
            this.toast.toastNotificacion('Publicación eliminada de colecciones');
           }
        } else {
          this.toast.toastError('No se pudo guardar, intentelo más tarde')
        }
      }, (err) => {
        this.toast.toastError('No se pudo guardar, intentelo más tarde')
      })

    }
  }

  async comentarios( publicacion, indice ) {
    const modal = await this.modalCtrl.create({
      component: IgtmplantillaPage,
      componentProps: {
        tipo: 'comentar',
        publicacion: publicacion._id,
        creador: publicacion.creador,
        comentarios: publicacion.comentarios
      }
    })
    await modal.present();

    const cerrado = await modal.onWillDismiss();
    if (cerrado) {
        this.statusbar.show();
        this.click = false;

        if (cerrado.data) {
          if (cerrado.data.exe) {
            this.publicaciones[indice].comentarios = cerrado.data.comentarios;

            if (cerrado.data.comentarios.length > 0) {
              this.publicaciones[indice].msg = `${cerrado.data.comentarios.length} comentarios...`;
            } else {
              this.publicaciones[indice].msg = 'No hay ningún comentario paraesta publicación';
            }
          }
        }
      }
  }


  async megusta(publicacion: any, tipo, i) {
    if (tipo === 'megusta') {
      this.publicaciones[i].megusta = true;
      this.publicaciones[i].valormg = parseFloat(this.publicaciones[i].valormg) + 1;
    } else {
      this.publicaciones[i].megusta = false;
      this.publicaciones[i].valormg = parseFloat(this.publicaciones[i].valormg) - 1;
    }
    const info = await this.almacenamiento.obtenerToken();
   
    if ( info ) {
      this.IGTMApi.meGusta(info.token, {tipo, id: publicacion._id, creador: publicacion.creador}).subscribe((data: any) => {
        if (data.exe) {

          if (info._id !== publicacion.creador._id) {
            if (tipo === 'megusta') {
              this.socket.emit('megusta', {
                id: publicacion._id,
                mensaje: `@${info.nusuario} le gusta tu publicación`,
                creador: publicacion.creador._id
              })
            }
          }

        } else {
          this.toast.toastError('No se pudo dar me gusta, intentelo más tarde')
        }
      }, (err) => {
        this.toast.toastError('No se pudo dar me gusta, intentelo más tarde')
      })

    }
  }

  async etiquetar() {
    const modal = await this.modalCtrl.create({
      component: IgtmplantillaPage,
      componentProps: {
        tipo: 'etiquetar'
      }
    })
    await modal.present();

    
  }

  async etiquetas( etiquetados ) {
    const modal = await this.modalCtrl.create({
      component: IgtmplantillaPage,
      componentProps: {
        tipo: 'etiquetados',
        etiquetados
      }
    })
    await modal.present();

    const cerrado = await modal.onWillDismiss();
    if (cerrado) {
        this.statusbar.show();
        this.click = false;
      }
  }

  async ver(archivos, indice, info) {
    
    if ( !this.click ) {
      this.click = true;
      const modal = await this.modalCtrl.create({
        component: IgtmplantillaPage,
        componentProps: {
          tipo: 'visualizador',
          archivos,
          indice,
          info
        }
      })
      await modal.present();
      const cerrado = await modal.onWillDismiss();
      if (cerrado) {
        this.statusbar.show();
        this.click = false;
      }
    }


  }

  async perfilNavegar( usuario ) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
        if (info.nusuario === usuario.nombreusuario) {
          this.router.navigate(['/perfil/', 's'])
        } else {
          this.router.navigate(['/visita/', usuario.nombreusuario, 's'])
        }
    }
}

async mglista(listado) {
  const modal = await this.modalCtrl.create({
    component: ModalMegustasComponent,
    componentProps: {
      listado: listado
    }
  })

  await modal.present();
}
  

}
